.mainContainer {
  display: flex;
  padding: 0 24px;
  min-height: 90vh;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-content: space-between;
}
.linkContainer {
  margin-left: 180px;
}
.navContainer {
  height: 54px;
  padding-top: 10px;
  background-color: var(--main-color);
}

.navLink {
  text-decoration: none;
  color: white;
  font-size: 24px;
  margin-right: 40px;
}

.activeNavLink {
  font-weight: bold;
}

.active {
  text-transform: uppercase;
}
